.App {
    background-color: #282c34;
    min-height: 100vh;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-content {
    padding-top: 10px;
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: space-around;
}

.thumbnail {
    margin: 5px;
    position: relative;
}

.poster {
    width: 200px;
    height: 300px;
    border-radius: 5px;
}

.thumbnail > img:hover {
    box-shadow: 0 0 2px 1px rgba(0, 140, 186, 0.5);
}

.thumbnail > .mask {
    position: absolute;
    bottom: 20px;
    width: 100%;
    background: rgb(0, 0, 0);
    background: rgba(0, 0, 0, 0.7);
}

.info {
    padding: 5px;
    line-height: 1 !important;
}

.info > h3 {
    text-shadow: -1px 0 #333, 0 1px #333, 1px 0 #333, 0 -1px #333, #000 0 0 5px;
    font-size: 12pt;
    margin: 0 0 5px;
    color: #fff;
}

.info small {
    text-shadow: -1px 0 #333, 0 1px #333, 1px 0 #333, 0 -1px #333, #000 0 0 5px;
    font-size: 10pt;
    color: #ddd;
}

.App-header {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: start;
}

.App-header-item {
    margin: 5px !important;
}